.btn {
  background-color: #035f90;
  margin: 0 auto;
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 200px;
  border-radius: 10px;
  transition-duration: 0.4s;
  border: none;
}
.btn:hover {
  background-color: #008080;
  box-shadow: 2px 2px 2px #282828;
}

.btnLink {
  color: #ffffff;
  width: 100%;
  font-size: 1.4rem;
  padding: 5px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
}

.btn_hollow {
  background-color: transparent;
  border: 2px solid #ffffff;
  color: #ffffff;
  height: 30px;
  width: 150px;
  border-radius: 5px;
  transition-duration: 0.4s;
}
.btn_hollow:hover {
  background-color: #ffffff;
  color: #035f90;
  font-weight: bold;
}